

function config (lang = 'en', url, jsonapi = false, rest = false) {
  return {
    method: 'get',
    //mode: 'cors',
    baseURL: process.env.API_URL + lang + '/' + (!rest ? process.env.API_BASE : ''),
    url: url + (jsonapi ? process.env.API_TYPE : ''),
    headers: {
      'Content-Type': 'application/json',
      //'Authorization': 'Bearer '+ Buffer.from(process.env.BASIC_AUTH_USERNAME + ":" + process.env.BASIC_AUTH_PASSWORD).toString('base64'),
      'Accept-Language': lang
    },
    auth: {
      username: process.env.BASIC_AUTH_USERNAME,
      password: process.env.BASIC_AUTH_PASSWORD
    }
    //responseType: 'json'
  }
};


module.exports.config = config;