import React from 'react';
import { Alert, Button } from 'reactstrap';
import { AvForm, AvField } from 'availity-reactstrap-validation';
import PropTypes from 'prop-types';
import FormValidation from '../../utils/form-validation';
import StringHandler from '../../utils/string-handler';
import Languages from '../../data/global/languages';
import form_api from '../../connectivity/api.form';
import contact_fr from '../../data/api/form--contact--fr';
import contact_en from '../../data/api/form--contact--en';

export default class ContactForm extends React.Component {
    contactApi = {"message" : ""};
    constructor(props) {
        super(props);
        this.state = {message: {type : "", content: ""}, sending: 0};
        this.handleValidSubmit = this.handleValidSubmit.bind(this);
        this.contactApi = this.props.lang === 'fr' ? contact_fr : contact_en;
    }
    static propTypes = {
        lang: PropTypes.string.isRequired
    };

    handleValidSubmit(event, values) {
        let lang = this.props.lang;
        this.setState({sending : 1});
        let data = {
            "contact_form":[{"target_id":"contact"}],
            "uid": [{"target_id": "contact" }],
            "subject":[{"value":values.subject}],
            "name":[{"value": values.first_name + ", " + values.last_name}],
            "mail":[{"value": values.mail}],
            "field_destinatione":[{"value": values.destination}],
            "message":[{"value": values.message}]
        };
        form_api.sendMessage(this.props.lang, 'contact', data).then(values => {
            this.setState({message: {type: "success", "content" : this.contactApi.message}, sending: 2});
        }).catch(e => {
            this.setState({ sending: 2, message: {type: "danger", "content" : StringHandler.stringI18n("Error occurred while sending to the server, please try again later.", lang)}});
        });
    }

    render() {
        let lang = this.props.lang;
        let self = this;
        return (
            <>
                {self.state.sending <= 1 ? (
                    <AvForm onValidSubmit={self.handleValidSubmit}>
                    <AvField name="subject" label={StringHandler.stringI18n("Subject", lang)} type="text" required errorMessage={FormValidation.validationMessage('required', lang)} />
                    <AvField name="first_name" label={StringHandler.stringI18n("First name", lang)} type="text" required errorMessage={FormValidation.validationMessage('required', lang)} />
                    <AvField name="last_name" label={StringHandler.stringI18n("Last name", lang)} type="text" required errorMessage={FormValidation.validationMessage('required', lang)} />
                    <AvField name="mail" label={StringHandler.stringI18n("Email", lang)} type="email" required errorMessage={FormValidation.validationMessage('required', lang)} />
                    <AvField type="select" label={StringHandler.stringI18n("Destination", lang)} name="field_destination" required errorMessage={FormValidation.validationMessage('required', lang)}>
                    <option value="">{StringHandler.stringI18n("-Select destination-", lang)}</option>
                    {Languages.informations[lang].destination.map(element => (
                        <option key={element.key} value={element.key}>{element.value}</option>
                    ))}
                    </AvField>
                    <AvField name="message" label="Message" type="textarea" required errorMessage={FormValidation.validationMessage('required', lang)} />
                    <Button color="primary" disabled={self.state.sending >= 1}>
                    {self.state.sending === 1 ? (<i className="fas fa-spinner fa-spin"/>) : null} {StringHandler.stringI18n("Submit", lang)}
                    </Button>
                    </AvForm>
                ) : (
                    <>
                        {self.state.message.content.length > 0 ? (
                            <Alert color={self.state.message.type}>
                                {self.state.message.content}
                            </Alert>
                        ): null}
                    </>
                )}
            </>
        );
    }
}