const request_config = require('./request-config');
const axios = require('axios');

function sendMessage(lang = 'en', form, data) {
  const config = request_config.config(lang, '/entity/'+form + '_message/', true, true);
  config.method = 'POST';
  config.data = data;
  return axios.request(config);
}

function getInformation(lang = 'en', form) {
    const config = request_config.config(lang, '/contact/'+form + '/', true, true);
    return axios.request(config);
}
  
module.exports.sendMessage = sendMessage;
module.exports.getInformation = getInformation;